export const Request = Object.freeze({
  Default: Object.freeze({
    HEADERS: Object.freeze({
      'Accept': 'application/json',
      'Content-Type': 'application/json; charset=utf-8',
      'Cache': 'no-cache'
    }),
    CREDENTIALS: 'include'
  })
});



